
<template>

    <div>
        <el-tag type="warning"  style="margin-left: 20px;margin-bottom: 10px;width: 200px;height: 40px;text-align: center;line-height: 40px;font-size: 20px">账号管理</el-tag>
        <el-col :span="23" style=" padding-bottom: 30px;">

            <el-row>
                <el-col :span="1.5" class="el-col" style="">
                    <p class="input-title">&nbsp;</p>
                    <el-button type="primary" icon="el-icon-plus"  @click = "addruntime">新增</el-button>
                </el-col>
            </el-row>
        </el-col>


        <el-col :span="23" style=" padding-top: 30px;">
            <table class="content-table">
                <tr>
                    <th>名称</th>
                    <th>账号</th>
                    <th>操作</th>
                </tr>
                <tr v-for="(item,index) in aaData">
                    <td>{{item.name}}</td>
                    <td>{{item.account}}</td>
                    <td>
                        <span class="dothis" @click="edit(item.account,item.name)">编辑</span>
                        <span class="dothis" @click="auth(item.account)">设置权限</span>
                        <span class="dothis" @click="del(item.account)">删除</span>
                    </td>
                </tr>
            </table>
            <div class="block page-f" style="position: relative;width: 100%">

                <!--<el-pagination-->
                    <!--style="position: absolute;right: 0"-->
                    <!--background-->
                    <!--@size-change="handleSizeChange"-->
                    <!--@current-change="handleCurrentChange"-->
                    <!--:current-page.sync="currentPage"-->
                    <!--:page-size="pageSize"-->
                    <!--:page-sizes="[10, 20, 30, 40]"-->
                    <!--layout="total,sizes,prev, pager, next, jumper"-->
                    <!--:total="totalNum">-->
                <!--</el-pagination>-->
            </div>
            <div style="height: 80px"></div>
        </el-col>

        <el-dialog title="新增账号" :visible.sync="addvisible">
            <el-form :model="addForm">
                <el-form-item label="名称" :label-width="formLabelWidth" >
                    <el-input  v-model="addForm.name" auto-complete="off " style="width: 300px" placeholder="请输入名称"></el-input>
                </el-form-item>
                <el-form-item label="账号" :label-width="formLabelWidth">
                    <el-input v-model="addForm.account" auto-complete="off" style="width: 300px" placeholder="请输入账号"></el-input>
                </el-form-item>
                <el-form-item label="密码" :label-width="formLabelWidth">
                    <el-input v-model="addForm.password" auto-complete="off " style="width: 300px" placeholder="请输入密码"></el-input>
                </el-form-item>

            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="addvisible = false">取 消</el-button>
                <el-button type="primary" @click="addsure" >确 定</el-button>
            </div>
        </el-dialog>

        <el-dialog title="编辑账号" :visible.sync="editvisible">
            <el-form :model="editForm">
                <el-form-item label="名称" :label-width="formLabelWidth" >
                    <el-input  v-model="editForm.name" auto-complete="off " style="width: 300px" placeholder="请输入名称"></el-input>
                </el-form-item>
                <el-form-item label="账号" :label-width="formLabelWidth">
                    <el-input v-model="editForm.account" auto-complete="off" style="width: 300px" placeholder="请输入账号"></el-input>
                </el-form-item>
                <el-form-item label="密码" :label-width="formLabelWidth">
                    <el-input v-model="editForm.password" auto-complete="off " style="width: 300px" placeholder="请输入新密码"></el-input>
                </el-form-item>

            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="editvisible = false">取 消</el-button>
                <el-button type="primary" @click="editsure" >确 定</el-button>
            </div>
        </el-dialog>

        <el-dialog title="设置权限" :visible.sync="authvisible">
            <el-form :model="authForm">
                <el-form-item label="权限" :label-width="formLabelWidth" >
                    <el-select v-model="authForm.modules" multiple placeholder="请选择" @change="authChange"  style="width: 300px">
                    <el-option
                        v-for="item in moduleOptions"
                        :key="item.id"
                        :label="item.modulename"
                        :value="item.id">
                    </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="供应商" :label-width="formLabelWidth" v-if="supplierShow" >
                    <el-select v-model="authForm.suppliers" multiple  placeholder="请选择"  style="width: 300px">
                    <el-option
                        v-for="item in supplierOptions"
                        :key="item.supplierId"
                        :label="item.supplierName"
                        :value="item.supplierId">
                    </el-option>
                    </el-select>
                </el-form-item>

            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="authvisible = false">取 消</el-button>
                <el-button type="primary" @click="authsure" >确 定</el-button>
            </div>
        </el-dialog>
    </div>
</template>
<script>
    import ajax from "../model/sys/index"
    export default {
        data() {
            return {
                value:'',
                currentPage: 1,//默认页码
                pageSize:10,//每页显示条数
                totalNum:10,//总条数
                aaData:[],
                supplierShow:false,
                addvisible:false,
                editvisible:false,
                authvisible:false,
                from:"",
                to:"",
                searchForm:{

                },
                addForm: {
                    account:"",
                    password:"",
                    name:""
                },
                editForm: {
                    account:"",
                    password:"",
                    name:""
                },
                authForm:{
                    account:"",
                    modules:[],
                    suppliers:[]
                },
                moduleOptions: [],
                supplierOptions:[],
                value5: [],
                formLabelWidth: '120px',

            };
        },
        methods: {
            getUsers:function () {
                var data  = this.searchForm
                data.pageStartIndex=this.currentPage
                data.pageRowsCount=this.pageSize
                for(var i in data){
                    if(String(data[i])==""){
                        delete data[i]
                    }
                }
                ajax.getalluser(data,res=>{
                    this.aaData = res.data.result
                })
            },

            edit(account,name){
                this.editvisible = true
                this.editForm={
                    account:account,
                    password:"",
                    name:name
                }
            },
            editsure(){
                for(var i in this.editForm){
                    if(String(this.editForm[i])==""){
                        this.$message({
                            showClose: true,
                            message: '所有输入框为必填',
                            type: 'fail'
                        });
                        return
                    }
                }
                ajax.edituser(this.editForm,res=>{
                    if(res){
                        this.editvisible = false
                        this.$message({showClose: true,message: '编辑成功',type: 'success'});
                        this.getUsers()
                    }

                })

            },
            auth(acount){
                ajax.userRoles({
                    account:acount,
                    token:localStorage.getItem("token")
                },res=>{
                    var modulesId = [],supplierId = []
                    for(var i=0;i<res.data.result.moduls.length;i++){
                        modulesId.push(res.data.result.moduls[i].id)
                    }
                    for(var i= 0;i<res.data.result.suppliers.length;i++){
                        supplierId.push(res.data.result.suppliers[i].supplierid)
                    }
                    this.authForm.modules = modulesId
                    this.authForm.suppliers = supplierId
                    if(modulesId.indexOf(2)==-1){

                       this.supplierShow=false
                    }else {
                        this.supplierShow=true
                    }
                    this.getSupplier()
                    this.getMoudel()
                    this.authForm.account =  acount
                    this.authvisible = true
                })

            },
            authsure(){
                this.authForm.modules = this.authForm.modules.sort(function(a,b){return a-b})

                ajax.bindauth(this.authForm,res=>{
                    if(res){
                        this.authvisible = false
                        this.$message({showClose: true,message: '成功',type: 'success'});
                        this.getUsers()
                    }

                })

            },
            addruntime(){
                this.addvisible = true
                for(var i in this.addForm){
                    this.addForm[i]=""
                }
            },
            addsure(){
                if(this.addForm.name==""){
                    this.$message({showClose: true,message: '名称为空',type: 'fail'});
                    return
                }
                ajax.adduser(this.addForm,res=>{
                    if(res){
                        this.addvisible = false
                        this.$message({showClose: true,message: '新增成功',type: 'success'});
                        this.getUsers()
                    }

                })
            },
            authChange(arr){

                if(arr.indexOf(2)==-1){
                    this.authForm.suppliers = []
                   this.supplierShow=false
                }else {
                   this.supplierShow=true
                }
            },

            del(account){
                this.$confirm('是否继续删除操作?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    ajax.deleteuser({account:account},res=>{
                        if(res){
                            this.$message({showClose: true,message: '删除成功',type: 'fail'});
                            this.getUsers()
                        }
                    })
                }).catch(() => {

                });

            },

            getMoudel:function () {
                ajax.adminmodules({},res=>{
                    if(res){
                        this.moduleOptions=res.data.result

                    }
                })
            },
            getSupplier:function () {
                ajax.adminsupplier({},res=>{
                    if(res){
                        this.supplierOptions=res.data.result

                    }
                })
            },
            handleSizeChange(val) {
                this.pageSize = val
                this.getUsers ()
            },
            handleCurrentChange(val) {
                this.currentPage = val
                this.getUsers ()
            }
        },
        mounted(){
            this.getUsers()

        }
    }
</script>
<style>
    .input-title{
        font-size: 14px !important;
        font-weight: 600;
    }
    .el-col{
        margin-left: 20px;
    }
    ::-webkit-scrollbar-thumb{
        background-color: gainsboro;
    }
    .cell{
        text-align: center;
    }
    #content-main{
        overflow: scroll;
    }
    .el-table{
        overflow: scroll;
    }
    .content-table{
        width: 100%;
        border-collapse:collapse;
        background-color: white;
        text-align: center;
    }
    .content-table th{
        height: 40px;
        border: 1px solid #F2F3F4;
        text-align: center;
        background-color: gainsboro;
    }
    .content-table td{
        height: 40px;
        border: 1px solid #F2F3F4;
    }
    .dothis{
        color: #3C9CFC;
        cursor:default;
        margin-left: 10px;
    }
    .page-f{
        margin-top: 20px;
        float: right;
    }
    .content-table span:hover{
        color: silver;
    }
    .el-col-23{
        width: 99% !important;
    }
</style>



